* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #fdfdfd;
  font-family: "Now";
  font-style: normal;
  font-weight: 400;
  font-size: 1.2vw;
  text-transform: uppercase;
  margin: auto;
  width: 65%;
  margin-left: 22%;
  position: relative;
  padding-top: 2vw;
  user-select: none;
}
#bg_blend_tnc {
  background: linear-gradient(180deg, #0e1391 0%, #0e0045 100%);
  height: 130vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -100;
}
*::selection {
  color: blue;
  background: white;
}
#zestxlogotcpage {
  position: fixed;
  top: 1.45vw;
  right: 4vw;
  z-index: 100;
  width: 7vw;
  height: 7vw;
  user-select: none;
}
.scrolling_terms_condition {
  position: fixed;
  top: 0;
  left: 0;
  border-right: 1px solid white;
  height: 80vh;
  width: 20%;
  padding-right: 1.5vw;
  margin-top: 5vw;
}

.scrolling_terms_condition_content {
  padding-top: 0.5vw;
  font-size: 1.2vw;
  text-align: right;
  transition: font-size 300ms ease-in-out;
  cursor: pointer;
}
.scrolling_terms_condition_content_active {
  color: pink;
  font-size: 1.3vw;
}
.terms_conditon_landing > h1:first-child {
  color: pink;
  font-size: 2vw;
}
section {
  line-height: 1.7;
  overflow-wrap: break-word;
  word-wrap: break-word;
  padding: 0.625vw 1.25vw 2.5vw;
}
section h1,
section h2,
section h3,
section h4,
section h5,
section h6 {
  font-weight: 500;
  margin-bottom: 0;
  padding-bottom: 0;
}

section h1 {
  font-size: 1.2vw;
  margin-top: 1.5625vw;
  margin-bottom: 0.9375vw;
  line-height: 1.4;
}
section h2 {
  font-size: 1vw;
  margin-top: 1.25vw;
  margin-bottom: 0.625vw;
}
section h3 {
  font-size: 0.9vw;
  margin-top: 1.25vw;
  margin-bottom: 0.625vw;
}
section h4 {
  font-size: 0.7vw;
  margin-top: 1.25vw;
  margin-bottom: 0.625vw;
}
section h5 {
  font-size: 0.6vw;
  margin-top: 1.25vw;
  margin-bottom: 0.625vw;
}
section h6 {
  font-size: 0.5vw;
  margin-top: 1.25vw;
  margin-bottom: 0.625vw;
}
section p {
  font-size: 1vw;
  margin-bottom: 0.625vw;
}
section ul {
  font-size: 1vw;
  list-style-type: disc;
  line-height: 2;
  margin-left: 1.875vw;
  margin-top: 0.9375vw;
  margin-bottom: 0.9375vw;
}
section a {
  color: pink;
  text-decoration: none;
  text-transform: lowercase;
}
@media only screen and (max-width: 1200px) {
  .tnc_progressbar,
  .tnc_scrollpath {
    display: none !important;
  }
}
html {
  scroll-behavior: unset;
}
body::-webkit-scrollbar {
  width: 0;
}
#scrollPath {
  position: fixed;
  top: 0;
  right: 0;
  width: 15px;
  z-index: -10;
  cursor: grab;
  height: 100%;
  transition: all 200ms ease 0s;
  background: rgba(255, 255, 255, 0.05);
}
#Progressbar {
  position: fixed;
  top: 0;
  right: 0;
  cursor: grab;
  z-index: 200;
  border-radius: 0 0 10px 10px;
  width: 15px;
  /* height: ; */
  background: linear-gradient(to top, rgb(210, 14, 236), rgb(11, 189, 202));
  animation: animate 5s linear infinte;
}
@keyframes animate {
  0%,
  100% {
    filter: hue-rotate(0deg);
  }
  50% {
    filter: hue-rotate(360deg);
  }
}
#Progressbar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgb(210, 14, 236), #94fff4);
  filter: blur(10px);
}
#Progressbar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgb(210, 14, 236), #00ffe7);
  filter: blur(30px);
}
